import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchColor } from '../../actions/searchAction';

import CustomStyles from '../../CustomMaterialUIStyles/FilterComponents';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

// const styles = {
// };

class ImgColor extends Component {
  constructor(props) {
    super(props);
    // this.state = {
    //   value: this.props.value
    // }
    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(e, index, val) {
    if (val.length === 0) {
      this.props.fetchColor([null])
    } else {  
      switch (true) {
        case ((val[0] === null) && (!(val[1] === 'undefined'))):
          this.props.fetchColor([val[1]])
          break;
        case (val[val.length - 1] === null):
          this.props.fetchColor([null])
          break;
        default:
          this.props.fetchColor(val)
      }
      this.props.fetchImages()
    }
  }

  render() {
    const colorArray = ["grayscale", "transparent", "red", "orange", "yellow", "green",
    "turquoise", "blue", "lilac", "pink", "white", "gray", "black", "brown"];

    const booleanCheck = (x) => {
      if (this.props.color && (this.props.color.indexOf(x) > -1)) {
        return true
      } else {
        return false
      }
    } ;

    let styles = {
      _dropDownMenuStyle: {},
    };

    if (this.props.designType === 5) {
      styles._dropDownMenuStyle.menuStyle = { 
        width: '100vw',
      };
      styles._labelSelectField = {
      };
      styles._rootSelectField = {
        borderBottom: '1px solid #fff',
        backgroundColor: '#f3f3f3',
      };
      styles._selectFieldMenuItem = {
        textTransform: 'uppercase',
      };
      styles._iconSelectField = {
        fill: '#888',
      };
    }

    const MenuItemColor = (
      colorArray.map((val) => (
        // <div className='filtersMenuItem'>
          <MenuItem
            className='filtersMenuItem'
            key={val}
            value={val}
            primaryText={val}
            checked={booleanCheck(val)}
            style={CustomStyles.selectFieldRootMenuItem}
            innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, styles._selectFieldMenuItem)}
          />
        // </div>
      ))
    );

    return (
      <SelectField
        name='color'
        className='SelectField'
        // value={(this.props.value === '') ? 'reset' : this.props.value}
        value={this.props.color}
        onChange={this.handleChange}
        multiple={true}
        maxHeight={300}
        // floatingLabelText='Colors'

        style={Object.assign({}, CustomStyles.rootSelectField, styles._rootSelectField)}
        labelStyle={Object.assign({}, CustomStyles.labelSelectField, styles._labelSelectField)}
        iconStyle={Object.assign({}, CustomStyles.iconSelectField, styles._iconSelectField)}
        underlineStyle={CustomStyles.underlineSelectField}
        dropDownMenuProps={Object.assign({}, CustomStyles.dropDownMenuProps, styles._dropDownMenuStyle)}
        listStyle={CustomStyles.selectFieldList}

        // fullWidth={true}
        // autoWidth={true}
      >
        {/* <div className='filtersMenuItem'> */}
          <MenuItem 
            className='filtersMenuItem'
            value={null} 
            label='all' 
            primaryText='ALL'
            style={Object.assign({}, CustomStyles.selectFieldRootMenuItem, { fontWeight: 'bold' })}
            innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, styles._selectFieldMenuItem)} 
          />
        {/* </div> */}
        <Divider inset={true} style={CustomStyles.menuDivider}/>
        {MenuItemColor  }
      </SelectField>
    )
  }
}

ImgColor.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchColor: PropTypes.func.isRequired,
  color: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  color: state.search.color,
})

export default connect(mapStateToProps, { fetchImages, fetchColor })(ImgColor)


