import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchImageType } from '../../actions/searchAction';

import CustomStyles from '../../CustomMaterialUIStyles/FilterComponents';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

// const styles = {
// };

class ImgType extends Component {
  render() {
    let styles = {
      _dropDownMenuStyle: {},
    };

    if (this.props.designType === 5) {
      styles._dropDownMenuStyle.menuStyle = {
        width: '100vw',
      };
      styles._labelSelectField = {
      };
      styles._rootSelectField = {
        borderBottom: '1px solid #fff',
        backgroundColor: '#f3f3f3',
      };
      styles._selectFieldMenuItem = {
        textTransform: 'uppercase',
      };
      styles._iconSelectField = {
        fill: '#888',
      };
    }

    return (
      <SelectField
        name='imageType'
        // autoWidth={true}
        className='SelectField'
        value={this.props.imageType}
        onChange={(e, index, value) => { this.props.fetchImageType(value); this.props.fetchImages() }}
        // floatingLabelText='Image type'
        style={Object.assign({}, CustomStyles.rootSelectField, styles._rootSelectField)}
        labelStyle={Object.assign({}, CustomStyles.labelSelectField, styles._labelSelectField)}
        iconStyle={Object.assign({}, CustomStyles.iconSelectField, styles._iconSelectField)}
        underlineStyle={CustomStyles.underlineSelectField}
        dropDownMenuProps={Object.assign({}, CustomStyles.dropDownMenuProps, styles._dropDownMenuStyle)}
        listStyle={CustomStyles.selectFieldList}
      >
        <MenuItem 
          className='filtersMenuItem'
          value='all'
          label='all'
          primaryText='ALL' 
          style={Object.assign({}, CustomStyles.selectFieldRootMenuItem, { fontWeight: 'bold' })}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, styles._selectFieldMenuItem)}
        />
        <Divider inset={true} style={CustomStyles.menuDivider} />
        <MenuItem
          className='filtersMenuItem'
          value='photo' 
          primaryText='photo' 
          style={CustomStyles.selectFieldRootMenuItem}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, styles._selectFieldMenuItem)}
        />
        <MenuItem
          className='filtersMenuItem'
          value='illustration' 
          primaryText='illustration' 
          style={CustomStyles.selectFieldRootMenuItem}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, styles._selectFieldMenuItem)}
        />
        <MenuItem
          className='filtersMenuItem'
          value='vector' 
          primaryText='vector' 
          style={CustomStyles.selectFieldRootMenuItem}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, styles._selectFieldMenuItem)}
        />
      </SelectField>
    )
  }
}

ImgType.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchImageType: PropTypes.func.isRequired,
  imageType: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  imageType: state.search.imageType,
})

export default connect(mapStateToProps, { fetchImages, fetchImageType })(ImgType)


